//@flow
import React,{ Component } from 'react'
import { Form, Input, Button, Upload, Tooltip, Popover, Icon, Select, Checkbox, message, Spin } from 'antd';
import { View } from 'react-web-dom'
import { formType, handleSubmitType } from '../../../../utils/flow'
import { Fetch } from '../../../../utils'
import { env } from '../../../../config/root'

const FormItem = Form.Item;
const { TextArea } = Input

type Props = {
    form:formType,
    hideModal:Function
}
type State = {
    fileList:Array<{}>,
    file:{}
}

@Form.create()
export default class AddVideoForm extends Component<Props,State> {
    state = {
        fileList:[],
        file:{},
        loading:false
    }
    handleSubmit = (e:handleSubmitType) => {
        e.preventDefault();
        const { form, hideModal } = this.props
        form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                this.setState({
                    loading:true
                })
                const { file } = this.state
                let formData = new FormData()
                formData.append('media',file)
                formData.append('title',values.title)
                formData.append('description',values.description)
                let url = `${env.domain}/admin/wechat/materialUploadVideo`
                Fetch.formData(url,formData)
                .then((e)=>{
                    console.log('handleChange',e);
                    if(e.code===0){
                        message.success('')
                        this.setState({
                            loading:false
                        })
                        hideModal(e.result,file)
                    }
                })
            }
        })
    }
    normFile = (e:{file:{status:string},fileList:Array<{}>}) => {
        console.log('Upload event:', e);
        if(e.file.status==='removed'){
            this.setState({fileList:[]})
        }else {
            e.fileList=[e.file]
            this.setState({fileList:e.fileList})
        }
        if (Array.isArray(e)) {
            return e;
        }
        return e && e.fileList;
    }
    render() {
        const { form, hideModal } = this.props
        const { getFieldDecorator } = form
        const { fileList, loading } = this.state;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 4 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 20 },
            },
        }
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 20,
                    offset: 4,
                },
            },
        }
        const formatContent = (
            <div>
                <p>mp4flvf4vwebm</p>
                <p>m4vmov3gp3g2</p>
                <p>RealPlayer rmrmvb</p>
                <p> wmvaviasf</p>
                <p>MPEG  mpgmpegmpets</p>
                <p>DV divdvdivx</p>
                <p> vobdatmkvswflavfcpkdiracramqtfliflcmod</p>
                <p>mp3aacac3wavm4aogg</p>
            </div>
        );
        const flowContent = (
            <div>
                <p>1.</p>
                <p>2.</p>
                <p>3.</p>
                <p>4.</p>
            </div>
        );
        return (
            <Spin tip="Loading..." spinning={loading}>
                <Form onSubmit={this.handleSubmit}>
                    <FormItem
                        {...formItemLayout}
                        label=""
                        extra={[
                            <p key='0'>
                                10Mmp4
                                <Popover
                                    content={formatContent}
                                    title=""
                                >
                                    <a></a>
                                </Popover>
                            </p>
                        ]}
                    >
                        {getFieldDecorator('upload', {
                            valuePropName: 'fileList',
                            getValueFromEvent: this.normFile,
                            rules: [{ required: true, message: '!'}],
                        })(
                            <Upload
                                showUploadList={false}
                                beforeUpload={beforeUpload}
                                customRequest={({file})=>{
                                    this.setState({
                                        file
                                    })
                                }}
                            >
                                <Button>
                                    <Icon type="upload" /> {fileList.length ? '' : ''}
                                </Button>
                            </Upload>
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=''
                        extra='121'
                        hasFeedback
                    >
                        {getFieldDecorator('title', {
                            rules: [{ required: true, message: '!'}],
                        })(
                            <Input />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=''
                    >
                        <span>
                            &nbsp;
                            <Tooltip title="">
                                <Icon type="question-circle-o" />
                            </Tooltip>
                        </span>
                    </FormItem>
                    {/* <FormItem
                        {...formItemLayout}
                        label=''
                    >
                        {getFieldDecorator('sort', {
                            initialValue:'lucy',
                            rules: [{ required: true, message: '!', whitespace: true, pattern: /^S{1,21}$/ }],
                        })(
                            <Select
                                style={{ width: 200 }}
                                // onChange={handleChange}
                            >
                                <OptGroup label="Manager">
                                    <Option value="jack">Jack</Option>
                                    <Option value="lucy">Lucy</Option>
                                </OptGroup>
                                <OptGroup label="Engineer">
                                    <Option value="Yiminghe">yiminghe</Option>
                                </OptGroup>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label={(
                            <span>
                                &nbsp;
                                <Tooltip title="">
                                    <Icon type="question-circle-o" />
                                </Tooltip>
                            </span>
                        )}
                        hasFeedback
                    >
                        {getFieldDecorator('tags', {
                            rules: [{ required: true, message: '!', whitespace: true }],
                        })(
                            <Select
                                mode="tags"
                                style={{ width: '100%' }}
                                onChange={(value)=>{
                                    console.log(`selected ${value}`)
                                }}
                                tokenSeparators={[',']}
                            />
                        )}
                    </FormItem> */}
                    <FormItem
                        {...formItemLayout}
                        label=''
                        hasFeedback
                    >
                        {getFieldDecorator('description', {
                            rules: [{ required: true, message: '!' }],
                        })(
                            <TextArea
                                // style={{ width: 240 }}
                                type="textarea"
                                autosize={{ minRows: 3, maxRows: 6 }}
                            />
                        )}
                    </FormItem>
                    <FormItem {...tailFormItemLayout} style={{ marginTop: 28 }}>
                        
                        <Popover
                            content={flowContent}
                            title=""
                        >
                            <a></a>
                        </Popover>
                    </FormItem>
                    <FormItem {...tailFormItemLayout} style={{ marginBottom: 8 }}>
                        {getFieldDecorator('agreement', {
                            valuePropName: 'checked',
                        })(
                            <Checkbox>  <a target="_blank" href="http://v.qq.com/help/help_agreement.html"></a></Checkbox>
                        )}
                    </FormItem>
                    <FormItem {...tailFormItemLayout}>
                        <Button
                            onClick={()=>{
                                this.props.hideModal()
                            }}
                        >
                            
                        </Button>
                        <Button
                            style={{marginLeft:'20px'}}
                            type="primary"
                            htmlType="submit"
                        >
                            
                        </Button>
                    </FormItem>
                </Form>
            </Spin>
       )
   }
}

function beforeUpload(file) {
  const isvideo = file.type === 'video/mp4';
  if (!isvideo) {
    message.error('!');
  }
  const isLt10M = file.size / 1024 / 1024 < 10;
  if (!isLt10M) {
    message.error('10M!');
  }
  return isvideo && isLt10M;
}
